*
{
    margin: 0;
    padding: 0;
}
.app
{
    text-align: center;
    color: blue;
}
.app input
{
    font-size: 14px;
    border-radius: 5px;
    outline: none;
    padding: 10px 5px;
    width: 200px;
}
button
{
    background-color: rgb(241, 43, 175);
    color: white;
    border: none;
    outline: none;
    margin-top: 10px;
    padding: 10px 30px;
    font-size: 16px;
    cursor: pointer;
}
nav{
    background-color: rgb(153,47,153);
    padding: 10px 0;
    display: flex;
    justify-content: center;
    margin-top: 5px;
}
nav ul {
    display: flex;
    list-style-type: none;
    justify-content: space-between;
    align-items: center;
    width: 40%;
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
}
nav ul li:hover{
    transform: scale(1.4);
}
.navActive
{
    color: chartreuse;
    font-size: 30px;
    font-weight: bold;
}
.app-container{
    background-color: blue;
    color: white;
    padding: 10px;
}